.catalog{
   &__container{
      grid-area: content;
   }
   
   &__header{
      display: flex;
      align-items: flex-end;
      margin-top: 30px;
      margin-left: 51px;
      &-title{
         font-family: Raleway;
         font-style: normal;
         font-weight: bold;
         font-size: 20px;
         line-height: 23px;
         font-feature-settings: 'pnum'
      }
      &-settings{
         margin-left: 107px;
         font-family: Raleway;
         font-style: normal;
         font-weight: 600;
         font-size: 12px;
         line-height: 127.7%;
         text-align: center;
         font-feature-settings: 'pnum' on, 'lnum' on;
         color: #2967FF;
      }
   } 
   &__categories{
      align-items: center;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      margin-top: 20px;
      &-blok{
         height:25px;
         background: #2967FF;
         border-radius: 40.9053px;
         color:rgb(250, 250, 244);
         display: flex;
         flex-direction: row;
         
         padding: 0 10px;
         margin: 0 5px 5px 0;
         p{
            padding: 0; 
            margin: 0 ;
         }
         &:hover{
            background: #fafbfd;
            outline: 2px solid  #2967FF;
            cursor: pointer;
            p{
               color:#2967FF; 
            }
            
         }
      }
      &-blok.active{
         background: #e41809;
         &:hover{
            background: #fafbfd;
            outline: 2px solid  #e41809;
            p{
               color:#e41809; 
            }
            
         }
      }
      
   }
   &__products{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      &-item{
         width: 214px;
         margin: 10px;
         &-img{
            display: flex;
            align-items: center;
            // width: 214px;
            // height: 138px;
            img{
               width: 214px;
               height: 138px;
            }
         }
         &-title{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 140%;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #2D2D2F;
            overflow: hidden;
            height: 40px;
         }
         &-price{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #2967FF;
            height: 26px;
            margin-top: 5px;
         }
         &-discount{
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            &-price{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 14px;
               line-height: 122%;
               font-feature-settings: 'pnum' on, 'lnum' on;
               color: #8D8D8E;
               text-decoration:line-through;

            }
            &-count{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 600;
               font-size: 14px;
               line-height: 140%;
               text-align: right;
               font-feature-settings: 'pnum' on, 'lnum' on;
               color: #FF2D87;
               margin-left: 15px;
            }
         }
         &-button{
            width: 214px;
            border: 1px solid #2967FF;
            box-sizing: border-box;
            border-radius: 26px;
            display: flex;
            align-items: center;
            justify-content: center;
            p{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 14px;
               line-height: 140%;
               
               font-feature-settings: 'pnum' on, 'lnum' on;
               color: #2967FF;
            }
            &:hover{
               background: #2967FF;
               cursor: pointer;
               p{
                  color:#f5f6f8; 
               }
               
            }
         }
      }
   }
}